.conteiner {
  /* display: inline-block; */
  width: 95%;
  padding-left: 10px;
  padding-top: 10px;
  line-height: 1.3;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@media only screen and (min-width: 1700px) {
  .block {
    width: 800px;
    height: 400px;
  }

 
  .header {
    font-size: 18px;
    line-height: 1.5;
  }
}
